<template>
  <div class="files-article">
    <div v-for="file in files" :key="file" class="article-container" @click="sellectFile(file)">
      <div class="file">
        <div class="file-name">{{ fileName(file) }}{{ file }}</div>
        <div class="send file-icon">
          <i class="material-icons">email</i>
        </div>
        <div class="delete file-icon" @click="$modal.show('hello-world')">
          <i class="material-icons">delete</i>
        </div>
      </div>
    </div>
    <div class="filler"></div>
  </div>
  <!--files-article-->
</template>

<script>
export default {
  name: "FilesTab",
  data() {
    return {
      files: Array
    };
  },
  created() {
    this.$store.commit("loadAll");
    this.files = this.$store.state.fileInfo.fileList;

    this.$store.subscribe(() => {
      this.$forceUpdate();
      this.files = this.$store.state.fileInfo.fileList;
    });
  },
  methods: {
    sellectFile(data) {
      this.$store.commit("changeFile", data);
      this.$emit("change-file");
    },
    fileName(data) {
      let name = this.$store.state.files[data].customerInfo.businessName;
      if (name.length) {
        return name + " - #";
      } else {
        return "Untitled - #";
      }
    },
    deleteFile() {
      let file = this.$store.state.fileInfo.activeFile;

      if (this.files.length === 1) {
        this.$store.commit("newFile");
      }

      if(this.$store.state.fileInfo.fileList[0] === file){
        this.$store.commit(
        "changeFile",
        this.$store.state.fileInfo.fileList[1]);
      }else{
        this.$store.commit(
        "changeFile",
        this.$store.state.fileInfo.fileList[0]);
      }
      
      this.$store.commit("deleteFile", file);
    }
  }
};
</script>