<template>
  <div class="quote-article">
    <div class="article-container container-sm">
      <div class="section-title h1">Customer Info</div>
      <div class="form">
        <div class="form-field">
          <i class="material-icons">domain</i>
          <input v-model="customerInfo.businessName" id="business-name" placeholder="Business Name" ref="top" @change="saveInfo()"/>
        </div>
        <div class="form-field">
          <i class="material-icons">person</i>
          <input v-model="customerInfo.contactName" id="customer-name" placeholder="Contact Name" @change="saveInfo()"/>
        </div>
        <div class="form-field">
          <i class="material-icons">email</i>
          <input
            v-model="customerInfo.contactEmailAddress"
            id="customer-email"
            placeholder="Email Address"
            @change="saveInfo()"
          />
        </div>
        <div class="form-field">
          <i class="material-icons">location_on</i>
          <input
            v-model="customerInfo.streetAddress"
            id="customer-address-1"
            placeholder="Street Address"
            @change="saveInfo()"
          />
        </div>
        <div class="form-field">
          <i class="material-icons">location_on</i>
          <input
            v-model="customerInfo.cityStateZip"
            id="customer-address-2"
            placeholder="City, State Zip"
            @change="saveInfo()"
          />
        </div>
      </div>
      <div class="section-title h1">Quote #{{ sellectedFile }}</div>
      <hr />
      <div>
        <button
          :class="[{'active' : periodic}, 'button', 'quote-type-button']"
          @click="changeFrequency('periodic'), saveInfo()"
        >Periodic</button>
        <button
          :class="[{'active' : frequent}, 'button', 'quote-type-button']"
          @click="changeFrequency('frequent'), saveInfo()"
        >Frequent</button>
      </div>
      <button class="quote-grid quote-form" />
      <div>
        <QuoteItem
          v-for="crane in cranes"
          :id="crane"
          :frequency="customerInfo.frequency"
          :key="crane"
        />
      </div>
      <div class="quote-item">
        <div></div>
        <div class="item-type-2">
          <div class="input-label-2">{{ hours }}</div>
          <div class="input-label">Hours at</div>
          <div class="input-label-2">$</div>
        </div>
        <div class="item-hourly">
          <input
            v-model="customerInfo.wagesPerHour"
            :class="[customerInfo.wagesPerHour && 'full', 'input', 'input-normal']"
            placeholder="100"
            @change="saveInfo()"
          />
          <div class="input-label">= $ {{ craneCost }}</div>
        </div>
      </div>
      <hr />
      <div class="quote-form">
        <div class="quote-item">
          <div class="plus-minus">
            <button class="plus" @click="increaseLiftNumber(), saveInfo()">
              <i class="material-icons">add</i>
            </button>
            <button class="minus" @click="decreaseLiftNumber(), saveInfo()">
              <i class="material-icons">remove</i>
            </button>
          </div>
          <div class="item-type-2">
            <input
              v-model="customerInfo.scissorLiftNumber"
              :class="[customerInfo.scissorLiftNumber && 'full', 'input', 'input-narrow']"
              placeholder="0"
              @change="saveInfo()"
            />
            <div class="input-label">Scissor Lifts</div>
            <div class="input-label-2">$</div>
          </div>
          <div class="item-hourly">
            <input
              v-model="customerInfo.scissorLiftCost"
              :class="[customerInfo.scissorLiftCost && 'full', 'input', 'input-normal']"
              :placeholder="$store.state.fileInfo.defaults.scissorLiftCost"
              @change="saveInfo()"
            />
            <div class="input-label">= $ {{ liftTotal }}</div>
          </div>
        </div>

        <div class="quote-item">
          <div></div>
          <div class="item-type-2">
            <input v-model="customerInfo.totalMiles" :class="[customerInfo.totalMiles && 'full', 'input', 'input-narrow']" placeholder="0" @change="saveInfo()"/>
            <div class="input-label">Total Miles</div>
            <div class="input-label-2">$</div>
          </div>
          <div class="item-hourly">
            <input v-model="customerInfo.costPerMile" class="input input-normal" :placeholder="$store.state.fileInfo.defaults.costPerMile" @change="saveInfo()"/>
            <div class="input-label">= $ {{ commuteCost }}</div>
          </div>
        </div>
      </div>
      <div class="total-tally">
        <div class="tally">
          <div>Fees = $</div>
          <input v-model="customerInfo.fees" :class="[customerInfo.fees && 'full', 'input', 'input-wide']" placeholder="0.00" @change="saveInfo()"/>
        </div>
        <div class="tally">
          <div>Discounts = $</div>
          <input v-model="customerInfo.discounts" :class="[customerInfo.discounts && 'full','input', 'input-wide']" placeholder="0.00" @change="saveInfo()"/>
        </div>
        <div class="mdc-form-field mdc-form-field--align-end">
          <div class="mdc-checkbox">
            <label for="checkbox-1">Show Cost Breakdown</label>
            <input :value="customerInfo.showCostBreakdown" type="checkbox" class="mdc-checkbox__native-control" id="checkbox-1" @click="toggleBreakdown(), saveInfo()"/>
            <!--<div class="mdc-checkbox__background">
              <svg class="mdc-checkbox__checkmark" viewBox="0 0 24 24">
                <path
                  class="mdc-checkbox__checkmark-path"
                  fill="none"
                  d="M1.73,12.91 8.1,19.28 22.79,4.59"
                />
              </svg>
              <div class="mdc-checkbox__mixedmark"></div>
            </div>-->
          </div>
          
        </div>
      </div>
      <hr />
      <div class="section-title h1 total-estimate">Total Estimated Cost = $ {{ totalCost }}</div>
      <div>
        <button class="button" @click="newFile()">
          <div class="new-button">
            <i class="material-icons">assignment</i>New
          </div>
        </button>
        <button class="button" @click="log()">
          <div class="send-button">
            <i class="material-icons">email</i>Send
          </div>
        </button>
      </div>
    </div>
  </div>
  <!--quote-article-->
  <!--There are two empty divs in here that were unfortunately necessary for the grid to display properly and be aligned with the rest of the page.-->
</template>

<script>
import _ from "lodash"; // Import the entire lodash library

import QuoteItem from "./QuoteItem";

export default {
  name: "QuoteTab",
  data() {
    return {
      sellectedFile: Number,
      periodic: true,
      frequent: false,
      customerInfo: {},
      cranes: [],
      hours: 0.0
    };
  },
  components: {
    QuoteItem
  },
  computed: {
    craneCost() {
      if (
        this.customerInfo.wagesPerHour === "" ||
        this.customerInfo.wagesPerHour === undefined
      ) {
        return this.hours * this.$store.state.fileInfo.defaults.wagesPerHour;
      } else {
        return this.hours * this.customerInfo.wagesPerHour;
      }
    },
    commuteCost() {
      if (
        this.customerInfo.costPerMile === "" ||
        this.customerInfo.costPerMile === undefined
      ) {
        return (
          this.customerInfo.totalMiles *
          this.$store.state.fileInfo.defaults.costPerMile
        );
      } else {
        return this.customerInfo.totalMiles * this.customerInfo.costPerMile;
      }
    },
    liftTotal() {
      if (this.customerInfo.scissorLiftCost === "") {
        return (
          this.customerInfo.scissorLiftNumber *
          this.$store.state.fileInfo.defaults.scissorLiftCost
        );
      } else {
        return (
          this.customerInfo.scissorLiftNumber *
          this.customerInfo.scissorLiftCost
        );
      }
    },
    totalCost() {
      let total = this.craneCost + this.liftTotal + this.commuteCost;
      if (this.customerInfo.fees != undefined && this.customerInfo.fees != "") {
        total += JSON.parse(this.customerInfo.fees);
      }
      if (
        this.customerInfo.discounts != undefined &&
        this.customerInfo.discounts != ""
      ) {
        total -= JSON.parse(this.customerInfo.discounts);
      }
      return total;
    }
  },
  mounted() {
    console.log("mounted");
    this.sellectedFile = this.$store.state.fileInfo.activeFile; // Initialize local vars
    this.customerInfo = _.cloneDeep(
      this.$store.state.files[this.sellectedFile].customerInfo
    );

    this.hours = 0;
    if (this.customerInfo.frequency === "periodic") {
      this.$store.state.files[this.sellectedFile].cranes.forEach(crane => {
        if (crane.number) {
          if (crane.periodicHours) {
            this.hours = this.hours + crane.number * crane.periodicHours;
          } else {
            this.hours =
              this.hours +
              crane.number *
                this.$store.state.fileInfo.cranes[crane.id].periodicHours;
          }
        }
      });
    } else {
      this.$store.state.files[this.sellectedFile].cranes.forEach(crane => {
        if (crane.number) {
          if (crane.frequentHours) {
            this.hours = this.hours + crane.number * crane.frequentHours;
          } else {
            this.hours =
              this.hours +
              crane.number *
                this.$store.state.fileInfo.cranes[crane.id].frequentHours;
          }
        }
      });
    }

    if (this.customerInfo.frequency === "periodic") {
      // Check frequency
      this.periodic = true;
      this.frequent = false;
    } else {
      this.periodic = false;
      this.frequent = true;
    }

    this.$store.state.fileInfo.cranes.forEach(crane => {
      // Populate cranes array with id's from store
      this.cranes.push(crane.id);
    });

    this.$store.subscribe(() => {
      // Watch for updaits to store

      this.hours = 0;
      if (this.customerInfo.frequency === "periodic") {
        this.$store.state.files[this.sellectedFile].cranes.forEach(crane => {
          if (
            crane.number &&
            this.$store.state.fileInfo.cranes[crane.id].active === true
          ) {
            if (crane.periodicHours) {
              this.hours = this.hours + crane.number * crane.periodicHours;
            } else {
              this.hours =
                this.hours +
                crane.number *
                  this.$store.state.fileInfo.cranes[crane.id].periodicHours;
            }
          }
        });
      } else {
        this.$store.state.files[this.sellectedFile].cranes.forEach(crane => {
          if (
            crane.number &&
            this.$store.state.fileInfo.cranes[crane.id].active === true
          ) {
            if (crane.frequentHours) {
              this.hours = this.hours + crane.number * crane.frequentHours;
            } else {
              this.hours =
                this.hours +
                crane.number *
                  this.$store.state.fileInfo.cranes[crane.id].frequentHours;
            }
          }
          this.$forceUpdate();
        });
      }

      if (this.$store.state.fileInfo.activeFile != this.sellectedFile) {
        // When file changes
        // Vars
        this.sellectedFile = this.$store.state.fileInfo.activeFile;
        this.customerInfo = _.cloneDeep(
          this.$store.state.files[this.sellectedFile].customerInfo
        );

        // Frequency
        if (this.customerInfo.frequency === "periodic") {
          this.periodic = true;
          this.frequent = false;
        } else {
          this.periodic = false;
          this.frequent = true;
        }
      }

      // Crane array
      if (this.cranes.length != this.$store.state.fileInfo.cranes.length) {
        let temp = [];
        this.$store.state.fileInfo.cranes.forEach(crane => {
          temp.push(crane.id);
        });
        this.cranes = temp;
      }
    });
  },
  methods: {
    dismissSoftKeybaord() {
      /*if (isIOS) {
        Frame.topmost().nativeView.endEditing(true);
      } else if (isAndroid) {
        utils.ad.dismissSoftInput();
      }*/
    },
    saveInfo() {
      this.$store.commit("saveInfo", this.customerInfo);
    },
    newFile() {
      this.$store.commit("newFile");
      this.$refs.top.focus();
    },
    log() {
      
    },
    changeFrequency(frequency) {
      this.customerInfo.frequency = frequency;
      if (frequency === "periodic") {
        // Check frequency
        this.periodic = true;
        this.frequent = false;
      } else {
        this.periodic = false;
        this.frequent = true;
      }
    },
    increaseLiftNumber() {
      if (typeof this.customerInfo.scissorLiftNumber === String) {
        this.customerInfo.scissorLiftNumber = JSON.parse(
          this.customerInfo.scissorLiftNumber
        );
      }
      this.customerInfo.scissorLiftNumber++;
    },
    decreaseLiftNumber() {
      this.customerInfo.scissorLiftNumber -= 1;
      if (this.customerInfo.scissorLiftNumber <= 0) {
        this.customerInfo.scissorLiftNumber = "";
      }
    },
    toggleBreakdown() {
      this.customerInfo.showCostBreakdown = !this.customerInfo.showCostBreakdown
    }
  }
};
</script>