<template>
  <div class="quote-item">
    <div class="plus-minus">
      <button class="plus" @click="increaseNumber(), saveFiles()">
        <i class="material-icons">add</i>
      </button>
      <button class="minus" @click="decreaseNumber(), saveFiles()">
        <i class="material-icons">remove</i>
      </button>
    </div>
    <div class="item-type">
      <input v-model="number" class="input input-narrow" placeholder="0" @change="saveFiles()" />
      <div class="input-label">Fixed hoists, spare hoists up to 3T</div>
    </div>
    <div v-if="frequency === 'periodic'" class="item-hourly">
      <input
        v-model="periodicHours"
        :class="[periodicHours && 'full', 'input', 'input-normal']"
        :placeholder="basePeriodicHours"
        @change="saveFiles()"
      />
      <div class="input-label">= {{ total }} hr</div>
    </div>
    <div v-if="frequency === 'frequent'" class="item-hourly">
      <input
        v-model="frequentHours"
        :class="[frequentHours && 'full', 'input', 'input-normal']"
        :placeholder="baseFrequentHours"
        @change="saveFiles()"
      />
      <div class="input-label">= {{ total }} hr</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      label: "hmm",
      active: true,

      number: "",
      periodicHours: "",
      frequentHours: "",
      sellectedFile: "",

      basePeriodicHours: "",
      baseFrequentHours: ""
    };
  },
  props: {
    id: Number,
    frequency: String
  },
  mounted() {
    this.active = this.$store.state.fileInfo.cranes[this.id].active;
    this.sellectedFile = this.$store.state.fileInfo.activeFile;
    this.label = this.$store.state.fileInfo.cranes[this.id].label;
    this.basePeriodicHours = this.$store.state.fileInfo.cranes[
      this.id
    ].periodicHours;
    this.baseFrequentHours = this.$store.state.fileInfo.cranes[
      this.id
    ].frequentHours;

    this.number = this.$store.state.files[this.sellectedFile].cranes[
      this.id
    ].number;
    if (this.number === 0) {
      this.number = "";
    }

    if (
      this.$store.state.files[this.sellectedFile].cranes[this.id]
        .periodicHours != this.periodicHours ||
      this.$store.state.files[this.sellectedFile].cranes[this.id]
        .frequentHours != this.frequentHours
    ) {
      this.periodicHours = this.$store.state.files[this.sellectedFile].cranes[
        this.id
      ].periodicHours;
      this.frequentHours = this.$store.state.files[this.sellectedFile].cranes[
        this.id
      ].frequentHours;
    }
    this.$store.subscribe(() => {
      this.active = this.$store.state.fileInfo.cranes[this.id].active;
      if (this.$store.state.fileInfo.activeFile != this.sellectedFile) {
        this.sellectedFile = this.$store.state.fileInfo.activeFile;
      }

      if (
        this.$store.state.files[this.sellectedFile].cranes[this.id].number !=
        this.number
      ) {
        this.number = this.$store.state.files[this.sellectedFile].cranes[
          this.id
        ].number;
        if (this.number === 0) {
          this.number = "";
        }
      }

      this.periodicHours = this.$store.state.files[this.sellectedFile].cranes[
        this.id
      ].periodicHours;
      this.frequentHours = this.$store.state.files[this.sellectedFile].cranes[
        this.id
      ].frequentHours;

      this.basePeriodicHours = this.$store.state.fileInfo.cranes[
        this.id
      ].periodicHours;
      this.baseFrequentHours = this.$store.state.fileInfo.cranes[
        this.id
      ].frequentHours;

      if (this.label != this.$store.state.fileInfo.cranes[this.id].label) {
        this.label = this.$store.state.fileInfo.cranes[this.id].label;
      }
    });
  },
  computed: {
    total() {
      if (this.frequency === "periodic") {
        if (this.periodicHours.length) {
          return this.periodicHours * this.number;
        } else {
          return this.basePeriodicHours * this.number;
        }
      } else {
        if (this.frequentHours.length) {
          return this.frequentHours * this.number;
        } else {
          return this.baseFrequentHours * this.number;
        }
      }
    }
  },
  methods: {
    increaseNumber() {
      if (typeof this.number === String) {
        this.number = JSON.parse(this.number);
      }
      this.number++;
    },
    decreaseNumber() {
      this.number -= 1;
      if (this.number <= 0) {
        this.number = "";
      }
    },
    saveFiles() {
      var payload = {};
      payload.id = this.id;
      payload.number = this.number;
      payload.periodicHours = this.periodicHours;
      payload.frequentHours = this.frequentHours;

      this.$store.commit("saveFiles", payload);
    }
  }
};
</script>