<template>
    <div class="settings-article">
    <div class="article-container container-sm">
      <div class="section-title h1">Axiom Hoist Settings</div>
      <div class="form">
        <div class="form-field">
          <i class="material-icons">domain</i>
          <input v-model="settings.companyName" id="company-name" placeholder="Company Name" @change="saveSettings()">
        </div>
        <div class="form-field">
          <i class="material-icons">location_on</i>
          <input v-model="settings.companyAddress1" id="company-address-1" placeholder="Company Address Line 1" @change="saveSettings()">
        </div>
        <div class="form-field">
          <i class="material-icons">location_on</i>
          <input v-model="settings.companyAddress2" id="company-address-2" placeholder="Company Address Line 2" @change="saveSettings()">
        </div>
        <div class="form-field">
          <i class="material-icons">phone</i>
          <input v-model="settings.companyPhone" id="company-phone" placeholder="Company Phone Number" @change="saveSettings()">
        </div>
        <div class="form-field">
          <i class="material-icons">email</i>
          <input v-model="settings.companyEmail" id="company-email" placeholder="Company Email Address" @change="saveSettings()">
        </div>
      </div>
      <div class="section-title h1">Quote Settings</div>
      <div class="quote-settings">
        <div class="quote-settings-number">
          <div class="input-label">Next Quote # = <input v-model="customFile" id="quote-number" class="input input-wide" :placeholder="nextFile" @change="saveSettings()"></div>
        </div>
        <button class="button blue-button">Edit Crane Categories and Defaults</button>
      </div>
      <div class="section-title h1">Email Settings</div>
      <div class="form">
        <div class="form-field">
          <i class="material-icons">local_offer</i>
          <input v-model="settings.email.subject" id="email-subject" placeholder="Email Subject" @change="saveSettings()">
        </div>
        <div class="form-field">
          <i class="material-icons">subject</i>
          <input v-model="settings.email.body" id="email-body" placeholder="Email Body" @change="saveSettings()">
        </div>
      </div>
      <div class="section-title h1">Export Settings</div>
      <div class="export-settings">
        <div class="export-settings-number">
          <div class="input-label">Quote Duration = <input id="duration" class="input input-narrow" placeholder="14" @change="saveSettings()"> days</div>
        </div>
        <button class="button red-button">Emergency Export All Files</button>
      </div>
    </div>
  </div><!--settings-article-->
</template>

<script>
export default {
    name: "SettingsTab",
    data() {
        return{
            nextFile: Number,
            customFile: "",
            settings: {
                companyName: "",
                companyAddress1: "",
                companyAddress2: "",
                companyPhone: "",
                companyEmail: "",
                email: {
                    subject: "",
                    body: ""
                }
            }
        }
    },
    mounted() {
        Object.assign(this.settings, this.$store.state.fileInfo.settings);
        this.nextFile = this.$store.state.fileInfo.nextFile
        this.customFile = this.$store.state.fileInfo.customFile

        this.$store.subscribe(() => {
            //Object.assign(this.settings, this.$store.state.fileInfo.settings);
            this.nextFile = this.$store.state.fileInfo.nextFile
            this.customFile = this.$store.state.fileInfo.customFile
        });
    },
    methods: {
        /*dismissSoftKeybaord() {
            if (isIOS) {
                Frame.topmost().nativeView.endEditing(true);
            }
            else if (isAndroid) {
                utils.ad.dismissSoftInput();
            }
        },*/
        clear() {
            this.$store.commit("clear");
        },
        saveSettings() {
            var payload = {}
            payload.settings = this.settings
            if(this.customFile.length) {
                payload.customFile = this.customFile;
            }else{
                payload.customFile = "";
            }

            this.$store.commit("saveSettings", payload);
        },
        /*editDefaults (args) {
            this.$navigateTo(Crane, {
                props: {
                    animated: true,
                    transition: {
                        name: "slide",
                        duration: 200,
                        curve: "ease"
                    }}});
        }*/
    }
}
</script>
