import Vue from "vue";
import Vuex from "vuex";
import _ from "lodash" // Import the entire lodash library

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        fileInfo: {
            activeFile: 1000,
            nextFile: 1000,
            customFile: "",
            fileList: [],
            settings: {
                companyName: "",
                companyAddress1: "",
                companyAddress2: "",
                companyPhone: "",
                companyEmail: "",
                email: {
                    subject: "",
                    body: ""
                }
            },
            cranes: [
                {
                    id: 0,
                    active: true,
                    label: "Fixed hoists, spare hoists up to 3T",
                    periodicHours: 0.75,
                    frequentHours: 0.50,
                },
                {
                    id: 1,
                    active: true,
                    label: "Fixed hoists over 3T",
                    periodicHours: 1,
                    frequentHours: 0.50,
                },
                {
                    id: 2,
                    active: true,
                    label: "Bridge Cranes p/p, Jib, Monorail, Gantry < 2T",
                    periodicHours: 0.75,
                    frequentHours: 0.50,
                },
                {
                    id: 3,
                    active: true,
                    label: "Bridge Cranes p/p, Jib, Monorail, Gantry 2T-5T",
                    periodicHours: 1,
                    frequentHours: 0.50,
                },
                {
                    id: 4,
                    active: true,
                    label: "Bridge Cranes 5T+ to 10T",
                    periodicHours: 1.5,
                    frequentHours: 0.50,
                },
                {
                    id: 5,
                    active: true,
                    label: "Bridge Cranes 10T+ to 30T",
                    periodicHours: 2,
                    frequentHours: 0.75,
                },
                {
                    id: 6,
                    active: true,
                    label: "Bridge Cranes 30T+ to 50T",
                    periodicHours: 3,
                    frequentHours: 0.75,
                },
                {
                    id: 7,
                    active: true,
                    label: "Bridge Cranes over 50T",
                    periodicHours: 6,
                    frequentHours: 1.50,
                }
            ],
            defaults: {
                scissorLiftCost: 150,
                costPerMile: 1.5,
                wagesPerHour: 100,
            }
        },
        sampleQuote: {
            customerInfo: {
                businessName: "",
                contactName: "",
                contactEmailAddress: "",
                streetAddress: "",
                cityStateZip: "",

                frequency: "periodic",
                wagesPerHour: "",
                scissorLiftNumber: "",
                scissorLiftCost: "",
                totalMiles: "",
                costPerMile: "",
                fees: "",
                discounts: "",
                showCostBreakdown: false,
            },
            cranes: [
                {
                    id: 0,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 1,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 2,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 3,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 4,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 5,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 6,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                },
                {
                    id: 7,
                    number: 0,
                    periodicHours: "",
                    frequentHours: "",
                }
            ],
        },
        files: {
        },
    },
    mutations: {
        newFile(state) {
            if (state.fileInfo.customFile.length) {
                let file = JSON.parse(state.fileInfo.customFile)
                /*if(state.files[file]){
                    dialogs.alert({
                        title: "A file with that name already exists.",
                        message: "Please change the files name and try again.",
                        okButtonText: "Ok"
                    }).then(result => {
                        
                    });
                    return
                }*/

                state.files[file] = JSON.parse(JSON.stringify(state.sampleQuote))// Creates a new object for the new file with the name of the nextFile

                state.fileInfo.fileList.push(file);// Adds this new files name to the array of currant files
                state.fileInfo.activeFile = file;
                state.fileInfo.customFile = "";
            } else {
                let file = state.fileInfo.nextFile

                state.files[file] = JSON.parse(JSON.stringify(state.sampleQuote)) // Creates a new object for the new file with the name of the nextFile
                state.fileInfo.fileList.push(state.fileInfo.nextFile); // Adds this new files name to the array of currant files
                state.fileInfo.activeFile = state.fileInfo.nextFile;
                state.fileInfo.nextFile += 2;
            }

            localStorage.setItem("fileInfo", JSON.stringify(state.fileInfo));
            localStorage.setItem("files", JSON.stringify(state.files));
        },
        loadAll(state) {
            if (localStorage.getItem("files")) {
                state.files = JSON.parse(localStorage.getItem("files"));
                state.fileInfo = JSON.parse(localStorage.getItem("fileInfo"));
            } else {
                let file = state.fileInfo.nextFile

                state.files[file] = JSON.parse(JSON.stringify(state.sampleQuote))
                state.fileInfo.fileList.push(state.fileInfo.nextFile);
                state.fileInfo.nextFile += 2;

                localStorage.setItem("files", JSON.stringify(state.files));
                localStorage.setItem("fileInfo", JSON.stringify(state.fileInfo));

            }
        },

        // Save things
        saveInfo(state, data) {
            Object.assign(state.files[state.fileInfo.activeFile].customerInfo, data);
            localStorage.setItem("files", JSON.stringify(state.files));
        },
        saveFiles(state, payload) {
            state.files[state.fileInfo.activeFile].cranes[payload.id].number = payload.number;
            state.files[state.fileInfo.activeFile].cranes[payload.id].periodicHours = payload.periodicHours;
            state.files[state.fileInfo.activeFile].cranes[payload.id].frequentHours = payload.frequentHours;
            localStorage.setItem("files", JSON.stringify(state.files));
        },
        saveSettings(state, payload) {
            state.fileInfo.settings = JSON.parse(JSON.stringify(payload.settings))
            state.fileInfo.customFile = payload.customFile;

            localStorage.setItem("fileInfo", JSON.stringify(state.fileInfo));
            console.log(payload)
        },
        saveFrequency(state, frequency) {
            state.files[state.fileInfo.activeFile].customerInfo.frequency = frequency
        },
        saveCraneCategory(state, payload) {
            state.fileInfo.cranes[payload.id].label = payload.label
            state.fileInfo.cranes[payload.id].active = payload.active
            state.fileInfo.cranes[payload.id].periodicHours = payload.periodicHours
            state.fileInfo.cranes[payload.id].frequentHours = payload.frequentHours
            localStorage.setItem("fileInfo", JSON.stringify(state.fileInfo));
        },

        changeFile(state, data) {
            state.fileInfo.activeFile = data;
        },
        clear() {
            localStorage.clear();
        },
        deleteFile(state, file) {
            // Delete file
            delete state.files[file];
            state.fileInfo.fileList = _.remove(state.fileInfo.fileList, function (n) { return n != file; });

            localStorage.setItem("fileInfo", JSON.stringify(state.fileInfo));
            localStorage.setItem("files", JSON.stringify(state.files));
        },
        log(state) {
            console.log(JSON.stringify(state.fileInfo.fileList))
        }
    },
    getters: {
        getFileName: (state) => (id) => {
            return state.files.find(file => file === id).customerInfo.businessName
        }
    }
});